.discover_container {
  position: relative;
  margin: auto;
  left: 0;
  right: 0;
  width: 100%;
  height: 100px;
}

.discover_container .search {
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 40px;
  height: 40px;
  background: #ffcb9a;
  border-radius: 50%;
  transition: all 1s;
  z-index: 4;
  box-shadow: 0 0 25px 0 #d9b08c(0, 0, 0, 0.4);
}
.discover_container .search:hover {
  cursor: pointer;
}
.discover_container .search::before {
  content: "";
  position: absolute;
  margin: auto;
  top: 15px;
  right: 0;
  bottom: 0;
  left: 15px;
  width: 5px;
  height: 2px;
  background: #116566;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  transition: all 0.5s;
}
.discover_container .search::after {
  content: "";
  position: absolute;
  margin: auto;
  top: -1px;
  right: 0;
  bottom: 0;
  left: -1px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 2px solid #116566;
  transition: all 0.5s;
}
.discover_container input {
  font-family: "Inconsolata", monospace;
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 20px;
  height: 40px;
  outline: none;
  border: none;
  background: #ffcb9a;
  color: #116466;
  text-shadow: 0 0 10px #d9b08c;
  padding: 0 80px 0 20px;
  border-radius: 30px;
  box-shadow: 0 0 25px 0 #d9b08c, 0 20px 25px 0 #d9b08c(255, 255, 255, 0.2);
  transition: all 1s;
  opacity: 0;
  z-index: 5;
  font-weight: bolder;
  letter-spacing: 0.1em;
}
.discover_container input:hover {
  cursor: pointer;
}
.discover_container input:focus {
  width: 300px;
  opacity: 1;
  cursor: text;
}
.discover_container input:focus ~ .search {
  right: -350px;
  background: #ffcb9a;
  z-index: 6;
}
.discover_container input:focus ~ .search::before {
  top: 0;
  left: 0;
  width: 25px;
}
.discover_container input:focus ~ .search::after {
  top: 0;
  left: 0;
  width: 25px;
  height: 2px;
  border: none;
  background: #116566;
  border-radius: 0%;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.discover_container input::-webkit-input-placeholder {
  color: #116466;
  opacity: 0.5;
  font-weight: bolder;

  color: #ffffff00;
}
.discover_container input:-ms-input-placeholder {
  color: #116466;
  opacity: 0.5;
  font-weight: bolder;

  color: #ffffff00;
}
.discover_container input::placeholder {
  color: #116466;
  opacity: 0.5;
  font-weight: bolder;

  color: #ffffff00;
}

